.weather-card {
  width: 20%;
  height: 70vh;
  background-color:rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 30px;
  margin-top: 2vh;
  // position: absolute;
  // top: 2.5vh;
  // right: 1.5vw;
  color: white;
  font-size: 1rem;
}

.date {
  margin: 0.5em;
}

.icon_weather {
  padding: 0;
}

.weather_description {
  margin: 0.5em;
}

.temperature {
  margin: 0.5em;
}

.t_feels {
  margin: 0.5em;
  text-transform: uppercase;
}